.services {
    margin-top: 100px;
    padding: 100px;

    background-color: var(--main-color-4);
    min-width: 100vw;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.services .ourServices {
    padding: 10px 20px;
    text-align: center;
    margin-bottom: 20px;
    background-color: var(--main-color-1);
    width: fit-content;
    color: #fff;
    border-radius: 30px;
    font-size: .8rem;
}

.services h1 {
    text-align: center;
    margin-bottom: 50px;
    background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.6rem;
    /* width: 100vw; */
    /* background-color: aqua; */
    /* animation: grad 2s alternate infinite; */
}

