.serv-card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.serv-item {
    /* background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2)); */
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    border-radius: 20px;
    /* padding: 50px; */
    overflow: hidden;
    /* background-color: aqua; */
    /* width: 90%; */
    /* height: 90%; */
    /* animation: rotate 3s linear infinite; */
}

.services .box {
    position: relative;
    width: 340px;
    height: max(calc(100dvh - 150px), 350px);
    background: #fff;
    border-radius: 8px;
    overflow: hidden;
}

.services .box::before {
    content: '';
    z-index: 1;
    position: absolute;
    top: -50%;
    left: -50%;
    width: 340px;
    height: max(calc(100dvh - 150px), 350px);
    transform-origin: bottom right;
    background: linear-gradient(0deg, transparent, var(--main-color-1), var(--main-color-1));
    animation: animate 6s linear infinite;
}

.services .box::after {
    content: '';
    z-index: 1;
    position: absolute;
    top: -50%;
    left: -50%;
    width: 340px;
    height: max(calc(100dvh - 150px), 350px);
    transform-origin: bottom right;
    background: linear-gradient(0deg, transparent, var(--main-color-2), var(--main-color-2));
    animation: animate 6s linear infinite;
    animation-delay: -3s;
}

@keyframes animate {
    100% {
        transform: rotate(360deg);
    }
}

.services .animate {
    position: absolute;
    inset: 3px;
    background: #fff;
    /* padding: 20px; */
    border-radius: 8px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
}


.serv-item>i {
    font-size: 3.7rem;
    color: var(--main-color-1);
    /* margin-bottom: 40px; */
    margin-bottom: calc((100dvh - 300px) / 10);
}

.serv-item .serv-title {
    font-size: 1.3rem;
    font-weight: bolder;
    background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}

.serv-item .serv-content {
    font-size: .7rem;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 10px;

    color: var(--main-color-5);
    width: 100%;
    padding: 20px;
}

.serv-item a {
    /* padding-top: 1.5rem; */
    font-size: .8rem;
    color: var(--main-color-2);

}

.serv-item a:hover {
    box-shadow: inset linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
}

@media (max-width: 1200px) {

    .services .box,
    .services .box::after,
    .services .box::before {
        width: 400px;

    }

    .serv-card {
        display: grid;
        grid-template-columns: repeat(2, 1fr);

    }

}

@media (max-width: 991px) {

    .services .box,
    .services .box::after,
    .services .box::before {
        width: 500px;

    }

    .serv-card {
        /* background-color: aqua; */
        display: grid;
        grid-template-columns: repeat(1, 2fr);
        /* grid-template-rows: repeat(3,1fr); */
    }

}

@media (max-width: 768px) {

    .services .box,
    .services .box::after,
    .services .box::before {
        width: 80vw;
        /* height: 450px; */
        /* scale: 0.9; */


    }

    .serv-card {
        margin-left: 40px;
    }

    .services .ourServices {
        margin-left: 40px;

    }

    .services h1 {
        margin-left: 40px;
        /* font-size: 20px; */

    }


}