/* Start Stats */
.stats {
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  min-height: 300px;
  /* background-color: aqua; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.stats::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255 255 255 / 95%);
}

.stats h2 {
  text-align: center;
  font-weight: bold;
  font-size: 1.6rem;
  /* width: 90%; */
  margin-bottom: 50px;
  /* margin: 0 auto 50px; */
  position: relative;
  background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.stats .container {
  /* background-color: red; */
  display: grid;
  width: 90%;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  /* padding: 10px auto; */
  gap: 20px;
}

.stats .box {
  border-radius: 10px;
  background-color: var(--main-color-4);
  padding: 30px 15px;
  text-align: center;
  position: relative;
  opacity: 0.8;
  /* margin: auto; */
  /* transform: translateX(4%); */
  /* width: 23%; */
  transition: var(--main-transition);
}

.stats .box::before,
.stats .box::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: var(--main-color-1);
  transition: 1s;
  height: 0;
}

.stats .box::before {
  top: 0;
  right: 0;
}

.stats .box::after {
  left: 0;
  bottom: 0;
}

.stats .box:hover {
  opacity: 1;
}

.stats .box:hover::before,
.stats .box:hover::after {
  height: 100%;
}

.stats .box i {
  color: var(--main-color-1);
  font-size: 1.5rem;

}

.stats .box .number {
  display: block;
  font-size: 2.5rem;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}

.stats .box .text {
  color: var(--main-color-2);
  font-style: italic;
  font-size: 1rem;
  font-weight: bold;
}

@media (max-width: 768px) {
  .stats .box{
      transform: translateX(5%);
  }
}
/* End Stats */