.img-container {
    position: relative;
    /* width: 300px;
    height: 100px; */
}

.loading-img {
    background-color: var(--main-color-4);
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    z-index: 5;
}

.img-spinner {
    border: 4px solid var(  --main-color-2);
    border-left-color: var(  --main-color-1);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spinimg 1s linear infinite;
}

@keyframes spinimg {
    to {
        transform: rotate(360deg);
    }
}

.img-container img {
    max-width: 100%;
    max-height: 100%;
    transition: opacity .3s;
}


