.linei {
    margin-top: 100px;
    width: 50vw;
    height: 10px;
    border-radius: 10px;
    background: linear-gradient(90deg, var(--main-color-1) -100%, var(--main-color-2));
    display: block;
    transform: translateX(50%);
    animation: color 1s infinite alternate;
}

@keyframes color {
    0% {
        background: linear-gradient(90deg, var(--main-color-1) -100%, var(--main-color-2));
    }

    10% {
        background: linear-gradient(90deg, var(--main-color-1) -80%, var(--main-color-2));
    }

    20% {
        background: linear-gradient(90deg, var(--main-color-1) -60%, var(--main-color-2));
    }

    30% {
        background: linear-gradient(90deg, var(--main-color-1) -40%, var(--main-color-2));
    }

    40% {
        background: linear-gradient(90deg, var(--main-color-1) -20%, var(--main-color-2));
    }

    50% {
        background: linear-gradient(90deg, var(--main-color-1) 0%, var(--main-color-2));
    }

    60% {
        background: linear-gradient(90deg, var(--main-color-1) 20%, var(--main-color-2));
    }

    70% {
        background: linear-gradient(90deg, var(--main-color-1) 40%, var(--main-color-2));
    }

    80% {
        background: linear-gradient(90deg, var(--main-color-1) 60%, var(--main-color-2));
    }

    90% {
        background: linear-gradient(90deg, var(--main-color-1) 80%, var(--main-color-2));
    }

    100% {
        background: linear-gradient(90deg, var(--main-color-1) 100%, var(--main-color-2));
    }

}

.about {
    /* margin-top: 50px; */
    padding-top: 100px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    gap: 50px;
    /* max-height: calc(100vh - 100px); */
    /* margin-bottom: 250px; */

}

.about-l,
.about-r {
    flex: 1 1 50%;
}

.about-l {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-r {
    /* background-color: aqua; */
    height: 100%;
    overflow-x: hidden;
    z-index: 88;
}

.about-l img {
    max-width: 90%;
    margin: 10px;
}

.about-r .about-us {
    background-color: var(--main-color-1);
    width: fit-content;
    padding: 10px 20px;
    border-radius: 20px;
    margin-bottom: 20px;
    color: var(--main-color-4);
    overflow-x: hidden;
    font-size: 0.6rem;

}

.about-r h1 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    font-weight: bolder;
    background: linear-gradient(90deg, var(--main-color-2), var(--main-color-1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about-r h5 {
    font-size: 0.5rem;
    color: var(--main-color-5);
    margin-bottom: 20px;

}

.about-r ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 50px;

}

.about-r ul li {
    padding-top: 40px;
    color: var(--main-color-5);
    font-size: 0.5rem;
}

.about-r ul li i {
    background-color: var(--main-color-1);
    border-radius: 50%;
    padding: 11px 15px;
    color: var(--main-color-2);
    margin-right: 10px;
    font-size: .7rem;
}

.summary {
    color: var(--main-color-5);
    border-left: 8px solid var(--main-color-1);
    padding: 20px;
    font-size: 0.5rem;
}


@media (max-width: 1200px) {


    .about-r {
        font-size: 15px;

    }

    .summary {
        border-left: 6px solid var(--main-color-1);
        padding: 15px;
    }
}


@media (max-width: 991px) {
    .about {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        /* margin-top: 150px; */
        /* background-color: aqua; */
    }


    .about-l,
    .about-r {
        /* flex: 1 1 100%; */
        width: 100%;
        /* font-size: 15px; */
    }


}

@media (max-width: 768px) {
    /* .about {
        padding-top: 10px;

    } */

    .about-r ul {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .about-l,
    .about-r {
        width: 90%;
        margin-left: 50px;
        /* font-size: 10px; */

    }
}
