.intro {
    padding: 120px 0px 10px 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* font-size: 90px; */
    /* gap: 50px; */
    min-height: calc(100vh - 100px);

    /* background-color: aqua; */
}


.intro-l,
.intro-r {
    flex: 1 1 50%;
    overflow: hidden;
}

.intro-r {
    z-index: -1;
}

.company-name {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: var(--main-color-2);
    overflow-x: hidden;

}

.company-purpose {
    font-size: 1.8rem;
    /* padding: 5px 0px; */
    /* width: 100vw; */
    width: 100%;
    font-weight: bolder;
    margin-bottom: 30px;
    overflow-x: hidden;
    /* background-color: aqua; */
    background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.company-content {
    font-size: 0.6rem;
    border-left: 5px solid var(--main-color-1);
    padding: 10px;
    color: var(--main-color-5);
    overflow-x: hidden;

}

.img-container {
    border-radius: 10px;  
}

.intro-r img {
    width: 100%;
    height: calc(100vh - 100px);
    z-index: -1;
    background-size: cover;
    border-radius: 10px;

}


@media (max-width: 991px) {
    .intro.container {
        padding-left: 10px;
        padding-right: 10px;
    }

    .intro {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        gap: 50px;
        padding-top: 30vh;
        /* background-color: aqua; */
        /* margin-left: 50px; */

    }




    .intro-l {
        margin: 10px 90px;
    }

    .intro-l,
    .intro-r {
        width: 85%;
        /* font-size: 15px; */


    }

    .intro-r img {
        max-width: 100%;
        height: 100%;


    }

}

@media (max-width: 768px) {
    .intro {
        padding-top: 25vh;
    }

    .intro-l {
        margin: 10px 3px;
        /* font-size: 10px; */

    }

    .intro-r {
        width: 100%;
        /* font-size: 10px; */

    }
}