.articles {
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.articles .main-title {
    padding: 10px 20px;
    text-align: center;
    margin-bottom: 20px;
    background-color: var(--main-color-1);
    width: fit-content;
    color: #fff;
    border-radius: 30px;

}

.articles .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
}

.articles .box {
    position: relative;
    margin-top: 50px;
    padding-top: 30px;
    background-color: white;
    border-radius: 10px;
    border: .1px solid var(--main-color-2);

    overflow: hidden;
    transition: transform 0.3s;
}

@media (max-width: 768px) {
    .articles .box {
        margin-left: 40px;
        /* border-top-left-radius: 100px;
        border-bottom-right-radius: 100px; */
    }

}


.articles .box:hover {
    /* box-shadow: -3px 10px 10px 3px rgb(0 0 0 / 20%); */
    transform: translateY(-10px);
}

.articles .box img {
    margin-left: 15px;
    width: 90%;
    max-width: 100%;
    height: 250px;
    border-radius: 10px;

}



.articles .box .state {
    font-size: .5rem;
    position: absolute;
    top: 50px;
    right: 50px;
    z-index: 99;

    background-color: var(--main-color-1);
    padding: 10px;
    width: fit-content;
    border-radius: 20px;
    color: white;
}




.articles .box .views {
    font-size: .5rem;
    /* font-weight: bolder; */
    position: absolute;
    top: 230px;
    left: 40px;
    z-index: 99;
    background-color: var(--main-color-1);
    padding: 10px;
    width: fit-content;
    border-radius: 20px;
    color: white;

}

.articles .box .info {
    padding: 20px;
    border-top: 1px solid #e6e6e7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: .6rem;
}

.articles .box .info div {
    color: var(--main-color-1);
    font-weight: bold;
}

.articles .box .info i {
    margin-right: 20px;
    color: var(--main-color-1);
}

.articles .box:hover .info i {
    animation: moving-arrow 0.6s linear infinite;
}

@keyframes moving-arrow {
    100% {
        transform: translateX(10px);
    }
}










.card-content {
    padding: 30px 15px 15px;
    border-bottom: 1px solid var(--main-color-2);
}

.card-price {
    color: var(--main-color-1);
    margin-bottom: 5px;
    font-size: 1rem;
}


.card-title {
    margin-bottom: 15px;
    background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    width: fit-content;
    font-size: .8rem;
}


.card-text {
    color: var(--main-color-5);
    font-size: .5rem;
    line-height: 1.8;
    margin-bottom: 25px;
    width: 80%;
}

.card-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    row-gap: 15px;
}

.card-item {
    padding-block: 5px;
    font-size: .8rem;
    color: var(--main-color-2);
}

.card-item span {
    text-transform: capitalize;
}

.card-item:not(:last-child) {
    padding-right: 15px;
    border-right: 1px solid var(--main-color-2);
    margin-right: 20px;
}



.card-item span {
    margin-top: 5px;
}

@media (max-width: 768px) {

    .articles .box img {
        height: 200px;
    }


        
    .articles .box .state {
        /* top: 50px; */
        right: 35px;
    }
    



    .articles .box .views {
        top: 180px;
    }
}

