.singleHouse {
    /* background-image: linear-gradient(90deg , #f3c4f2 ,#efc781); */
    min-width: 100vw !important;
    min-height: 150vh;
    padding-top: 200px;


}

.oneHouse {
    margin-bottom: 50px;
    position: relative;
    display: flex;
    justify-content: center;
    gap: 50px;
    min-height: 120vh;

}

.oneHouse .house-l .img-container {
    border-radius: 10px;
    width: 100%;
    height: 80%;
}

.oneHouse .house-l img {
    width: 100%;
    height: 80%;
    border-radius: 20px;
}

.oneHouse .house-l,
.oneHouse .house-r {
    flex: 1 1 40%;
    font-size: .8rem;
}


.singleHouse button {
    margin-top: 50px;
    padding: 20px;
    background-color: var(--main-color-1);
    border-radius: 10px;
    margin-bottom: 50px;
    color: white;
    font-weight: bolder;
    margin-left: 50px;
}

.singleHouse button:hover {
    opacity: 0.9;
    outline: 5px solid var(--main-color-2);
}

@media (max-width: 992px) {
    .oneHouse {
        display: flex;
        flex-direction: column;
    }

    .oneHouse .house-l,
    .oneHouse .house-r {
        margin-left: 50px;
        overflow-x: hidden;
    }

}

.oneHouse .house-r {
    color: #222;
    word-spacing: 3px;
    font-weight: bold;
    line-height: 25px;
}


.blur1 {
    background: var(--main-color-2);
    width: 22rem;
    height: 30rem;
    right: -100px;
    top: -100px;
}


.blur2 {
    background-image: linear-gradient(0deg, var(--main-color-2), var(--main-color-1));
    width: 22rem;
    height: 30rem;
    right: 30vw;
    top: 60vh;
}

.slider {
    /* margin-top: 100px; */

}

#title,
#bp-wrapper {
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
}

.swiper-container {
    width: 480px;
    background-color: aqua;
}

#bp-640,
#bp-768 {
    display: none;
}

@media screen and (min-width: 640px) {
    .swiper-container {
        width: 640px;
    }

    #bp-480,
    #bp-768 {
        display: none !important;
    }

    #bp-640 {
        display: inline !important;
    }
}

@media screen and (min-width: 768px) {
    .swiper-container {
        width: 768px;
    }


    #bp-480,
    #bp-640 {
        display: none !important;
    }

    #bp-768 {
        display: inline !important;
    }
}

.swiper-pagination {
    bottom: 0;
    padding-bottom: 10px;
}

.swiper-wrapper {
    padding-inline-start: 0;
}

#thumbs.swiper-container {
    margin-bottom: 20px;
}

#thumbs .swiper-slide {
    cursor: pointer;
}


@media (max-width: 768px) {
    .slider {
        margin-left: 20px;
        width: 100%;
        scale: 0.8;
    }

    .oneHouse .house-r {
        font-size: 15px;
    }

}