/* Start Footer */
.footer {
  background-color: var(--main-color-3);
  padding-top: 50px;

}

@media (max-width: 767px) {
  .footer {
    text-align: center;
    /* transform: translateX(%); */
    /* padding-left: 30px; */

  }

  .footer .box .social {
    justify-content: center;
  }

}

.footer .container {
  /* margin: 0; */
  /* padding: 0; */
  display: grid;
  /* background-color: aqua; */
  /* justify-content: center; */
  /* align-items: center; */
  /* place-items: center; */
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 50px;
}

.footer .box {
  margin-left: 35px;
}

.footer .box h3 {
  /* background-color: aqua; */
  color: white;
  font-size: 2.5rem;
  margin: 0 0 20px;
}

.footer .box .social {
  display: flex;
}


.footer .box .social li {
  margin-right: 10px;
}

.footer .box .social li a {
  background-color: #313131;
  color: #b9b9b9;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 1rem;
  transition: var(--main-transition);
}

.footer .box .social .facebook:hover ,.footer .box .social .twitter:hover ,.footer .box .social .linkedin:hover{
  background-color: var(--main-color-2);
  color: var(--main-color-1);
}


.footer .box .text {
  padding-top: 30px;
  line-height: 2;
  color: #b9b9b9;
  font-size: .6rem;
}

.footer .box .links li {
  cursor: pointer;
  padding: 15px 0;
  font-size: .7rem;
  transition: var(--main-transition);
}

.footer .box .links li:not(:last-child) {
  border-bottom: 1px solid #444;
}

.footer .box .links li:hover {
  padding-left: 10px;
}

.footer .box .links li:hover a {
  color: white;
}

.footer .box .links li a {
  color: var(--main-color-5);
  transition: var(--main-transition);
}

.footer .box .links li a::before {
  font-family: "Font Awesome 5 Free";
  content: " >> ";
  font-weight: bolder;
  margin-right: 10px;
  color: var(--main-color-2);
}

.footer .box .line {
  display: flex;
  align-items: center;
  color: #b9b9b9;
  margin-bottom: 30px;
  font-size: .75rem;
}

@media (max-width: 767px) {
  .footer .box .line {
    flex-direction: column;
  }
}

.footer .box .line i {
  font-size: 1rem;
  color: var(--main-color-2);
  margin-right: 10px;
}

@media (max-width: 767px) {
  .footer .box .line i {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.footer .box .line .info {
  line-height: 1.7;
  flex: 1;
}

.footer .box .line .info span {
  display: block;
}

.footer .footer-gallery img {
  width: 45%;
  border: 3px solid var(--main-color-2);
  margin: 5px;
}

.footer .copyright {
  padding: 20px 0;
  text-align: center;
  color: white;
  margin: 20px 0 0;
  font-size: .6rem;
  border-top: 1px solid #444;
}

/* End Footer */