/* Start Header */
header {
    /* border-bottom: 2px solid #777; */
    box-shadow: 0px 0px 5px #212529;
    position: fixed;
    background-color: white;
    width: 100vw;
    z-index: 99999;
}

header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    gap: 35px;


}

.logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.l-name {
    color: var(--main-color-1);
    font-weight: bolder;

}

header .container .logo {
    padding: 5px 35px 0 0;
    width: 150px;
    height: 100px;
    font-size: .6rem;
}

header .container nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    /* overflow: hidden; */

}

header .container nav ul {
    width: 100%;
    display: flex;
    font-weight: bold;
}

header .container nav ul a {
    display: block;
    height: 100px;
    padding: 40px 10px;
    color: var(--main-color-3);
    transition: color 0.5s;
    cursor: pointer;
    font-size: .7rem;

}

