.hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
}

.hvr-underline-from-center:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: var(--main-color-1);
    height: 4px;
    -webkit-transition-property: left, right;
    transition-property: left, right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-underline-from-center:hover:before,
.hvr-underline-from-center:focus:before,
.hvr-underline-from-center:active:before {
    left: 0;
    right: 0;
}


header .container nav .contact {
    width: 170px;
    height: 55px;
    border-radius: 10px; 
    position: relative;
    /* scale: 0.9; */
    /* transition: background 1s; */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    text-align: center;

}

header .container nav .contact a {
    color: white;
    font-weight: bold;
    font-size: .67rem;
    width: 100%;
    height: 75%;
    padding: 11px;
    border-radius: 10px; 

    background: var(--main-color-3);
    transition: width .4s linear;

    /* inset: 3px; */

}

header .container nav .contact::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px; 
    background: linear-gradient(90deg, var(--main-color-1), var(--main-color-2));
    z-index: -8;
}


header .container nav .contact a:hover{
  width: 90%;
}




/************************* 
  start responsive header
*************************/
@media (max-width: 991px) {
    header .container nav {
        /* display: none; */
        width: 0;
        position: absolute;
        flex-direction: column;
        justify-content: flex-start;
        top: 100%;
        right: 0;
        background: linear-gradient(45deg, var(--main-color-2), var(--main-color-1));
        min-height: calc(100dvh - 100px);
        overflow: hidden;
        transition: width 0.2s ease-in-out;
    }

    header .container nav ul {
        flex-direction: column;
        width: 100%;
        /* padding: 20px; */
        /* font-size: 1em; */
    }

    header .container nav ul li {
        overflow: hidden;
        /* border-bottom:2px solid red ; */
        /* transition: 1s; */


    }

    /* header .container nav ul li:hover {
        background-color: var(--main-color-1);
    } */

    header .container nav ul a {
        padding-left: 20px;
        padding-top: calc((80dvh - 100px - 125px) / 7);
        color: var(--main-color-4);
        display: inline;
        height: 20px;


    }

    header .container nav button {
        margin-top: calc((80dvh - 100px - 300px) / 7);
        /* margin-bottom: calc((80dvh - 100px - 125px) / 7); */

        /* font-size: 1em; */
        margin-right: 10px;
        width: 40vw;
    }

    header .container nav.show {
        width: 50%;
    }

    .hh {
        text-decoration: none;
    }

    /************************* 
  start burger menu
*************************/
    .container .burger {
        position: absolute;
        right: 50px;
        cursor: pointer;
        padding: 18px 40px 21px 6px;
        box-shadow: 1px 1px 3px #a2a0a5;
        border-radius: 3px;
        transition: box-shadow 0.3s ease-in-out;
        background-color: var(--main-color-1);
    }

    .container .burger span,
    .container .burger span:before,
    .container .burger span:after {
        content: "";
        border-radius: 1rem;
        height: 4px;
        width: 35px;
        background-color: var(--main-color-4);
        position: absolute;
        display: block;
        transition: all 300ms ease-in-out;
        box-shadow: inset 0 0 3px #f3f3f3;
    }

    .container .burger span:before {
        top: -10px;
    }

    .container .burger span:after {
        bottom: -10px;
    }

    .container .burger.active {
        box-shadow: none;
    }

    .container .burger.active span {
        background-color: transparent;
        box-shadow: none;
    }

    .container .burger.active span:before,
    .container .burger.active span:after {
        top: 0;
        width: 35px;
    }

    .container .burger.active span:before {
        transform: rotate(45deg);
    }

    .container .burger.active span:after {
        transform: rotate(-45deg);
    }

    /************************* 
  end burger menu
*************************/

}

@media (max-width: 765px) {
    header .container .logo {
        height: 75px;
    }

    .container .burger {
        right: 20px;
    }

    .container {
        padding-left: 10px !important;
        /* scale: 0.85; */
    }

    .logo,
    .burger {
        scale: 0.85;
    }

    header .container nav {
        min-height: calc(100dvh - 75px);


    }

    /* header .container nav ul,
    header .container nav button {
        font-size: .8em;
    } */

    header .container nav ul a {
        padding-top: calc((80dvh - 75px - 125px) / 7);
    }

    header .container nav button {
        margin-top: calc((80dvh - 75px - 300px) / 7);
        /* margin-bottom: calc((80dvh - 50px - 125px) / 7); */
    }

}


/************************* 
  end responsive header
*************************/


