.blur {
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}

.loading {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(90deg, var(--main-color-2), var(--main-color-1));
  opacity: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading::before {
  content: "";
  width: 40px;
  height: 40px;
  border: 15px solid var(--main-color-2);
  border-top: 15px solid var(--main-color-1);
  border-bottom: 15px solid var(--main-color-1);
  border-radius: 50%;
  z-index: 999999999;
  animation: spinner 1s linear infinite;
}


@keyframes spinner {
  0% {
      transform: rotate(0deg);
  }

  100% {
      transform: rotate(360deg);
  }
}