.list {
    padding-top: 100px;
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.list img {
    width: 100%;
    height: calc(100dvh - 100px);
    object-fit: cover;

}

.list .search {
    position: absolute;
    top:75px;
    margin: 20px auto;
    width: 90vw;
    /* border: 2px solid var(--main-color-3); */
    /* box-shadow: 3px 3px 3px #333; */
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100dvh - 75px);
    flex-wrap: wrap;
    gap: 20px;

}

.list .search .searchBox {
    flex: 1 1 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 3px 3px 3px #111;
    /* width: 20%; */
    background-color: var(--main-color-4);
    gap: 50px;
    height: 70px;
    font-size: 1rem;
    /* margin: 20px; */
    border-radius: 10px;

}

.list .search .searchBox:hover {
    outline: 2.5px solid var(--main-color-1);
    border-radius: 10px;
}


.list .search .searchBox i {
    padding-top: 10px;
    color: var(--main-color-1);

}


.list .search .searchBox .input {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-direction: column;
}


.list .search .searchBox .input label {
    font-size: .5rem;
    color: var(--main-color-5);
}


.list .search .searchBox .input select {
    width: 100px;
    border: 2px solid var(--main-color-1);
    border-radius: 3px;
}



.list .search a {
    padding-top: 5px;
    color: var(--main-color-4);
    background-color: var(--main-color-1);
    width: 100px;
    height: 70px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.list .search a:hover {
    opacity: 0.9;
    box-shadow: 3px 3px 3px #111;

}

.list .search a:active {
    background-color: var(--main-color-3);

}

@media (max-width: 992px) {
    .list .search {
        display: flex;
        /* flex-direction: column; */

    }

    .searchBox {
        flex: 1 1 40%;
    }


    .list .search .searchBox {
        /* width: 60%; */
        padding: 10px;
    }

    .list .search a {
        flex: 1 1 100%;
        padding: 20px;
        /* width: 60%; */
        margin-left: 8px;
    }
}


@media (max-width: 768px) {
    .list {
        padding-top: 75px;
    }

    .list img {
        /* width: 100%; */
        height: calc(100dvh - 75px);
    }
    .list .search a ,.searchBox{
        flex: 1 1 100%;
   
    }
}