/* Start Global Rules */
:root {
    --main-color-1: #8a1e86;
    --main-color-2: #fcb43a;
    --main-color-3: #212529;
    --main-color-4: #eee;
    --main-color-5: #888;
    --main-transition: 0.3s;


}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

html {
    scroll-behavior: smooth;
    font-size: 20px;

}

body {
    font-family: "poppins" system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    overflow-x: hidden !important;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

button {
    border: none;
}

.container {
    padding-left: 50px;
    padding-right: 50px;
    margin-left: auto;
    margin-right: auto;
}

::-webkit-scrollbar {
    width: 15px;
    cursor: pointer;
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(var(--main-color-1), var(--main-color-2));
    border-radius: 10px;
    height: 40dvh;
    cursor: pointer;

}


@media (min-width: 768px) {

    /* .container {
        width: 750px;
    } */
    html {
        font-size: 20px;
    }
}


@media (min-width: 992px) {
    .container {
        width: 970px;
    }

    html {
        font-size: 22px;
    }
}

@media (min-width: 1200px) {
    .container {
        width: 1200px;
    }

    html {
        font-size: 25px;
    }
}

/* End Global Rules */